import $ from 'jquery'
import UIkit from 'uikit'
//import places from 'places.js'
import TextRotate from './text-rotate'
// import shave from 'shave'



window.onload = function () {
    import('uikit/dist/js/uikit-icons').then((Icons) => {
        UIkit.use(Icons);
    });
    import('places.js').then((places) => {
        // Place auto complete places widget
        let placesAutoComplete = places({
            container: document.querySelector('#address-input'),
            language: 'fr',
            countries: ['fr'],
            //useDeviceLocation: true
        });
        // @TODO send data to the server
        placesAutoComplete.on('change', function(e) { console.log(e.suggestion) });
    });
    /**
     * Home page hero slider
     * @TODO add lazy loading of slick carousel (must be loaded only on home page)
     */
    import('slick-carousel').then(() => {
        $('.hero-slider').slick({
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            arrows: false,
            autoplay: true,
            autoplaySpeed: 4000,
            fade: true,
            cssEase: 'linear',
            pauseOnHover: false
        })
    });
    let elements = document.getElementsByClassName('txt-rotate');
    for (let i = 0; i < elements.length; i++) {
        let toRotate = elements[i].getAttribute('data-rotate');
        let period = elements[i].getAttribute('data-period');
        if (toRotate) {
            new TextRotate(elements[i], JSON.parse(toRotate), period);
        }
    }
};


